// @flow
import {
  Box,
  Button,
  Center,
  Checkbox,
  Divider,
  Flex,
  Heading,
  Icon,
  Image,
  Link,
  Spinner,
  Text,
  useDisclosure,
  VStack,
} from "@chakra-ui/react";
import Images_Icons from "../../../constants/icons-images";
import { lazy, FC, Suspense, useEffect, useState } from "react";
import { useStyles } from "../Login/Login";
import { Formik, FormikValues } from "formik";
import { AuthReducerProps } from "../../../interfaces";
import { useNavigate } from "react-router-dom";
import { GLOBAL_PREFIX, RegisterValidationSchema } from "../../../constants";
import { registerFn, resetErrorMsg, resetLoaders } from "../../../redux/AuthSlice";
import { useDispatch, useSelector } from "react-redux";
import { Dispatch } from "redux";
import { RootState } from "../../../redux/store";
import { FaFacebookSquare, FaTwitter } from "react-icons/fa";
import axios from "axios";
import { useOnline } from "rooks";
import { BASE_URL } from "../../../config";
import OTPModal from "./OTPModal";

const MeProtocolFormikButton = lazy(() => import("../../../components/MeProtocolFormikButton"));
const MeProtocolButton = lazy(() => import("../../../components/MeProtocolButton"));
const MeProtocolInput = lazy(() => import("../../../components/MeProtocolInput"));
const CountrySelector = lazy(() => import("../../../components/CountrySelector"));
const TopIconBar = lazy(() => import("../../../components/TopIconBar"));
export const Register: FC = () => {
  const dispatch = useDispatch<Dispatch>();
  let navigate = useNavigate();
  const online = useOnline();
  const [show, setShow] = useState(false);
  const [googleLoading, setGoogleLoading] = useState<boolean>(false);
  const { isOpen, onClose, onOpen } = useDisclosure();
  const [codeJwt, setCodeJwt] = useState<string>("");
  const [errorMsg, setErrorMsg] = useState<string>("");

  const handleClick = () => setShow(!show);
  const { token, registerLoading }: AuthReducerProps = useSelector(
    (state: RootState) => state.authReducer
  );
  const {
    root,
    boxLeft,
    boxRegister,
    leftImg,
    registerColumn,
    head,
    desc,
    basedText,
    checkBoxRoot,
    countrySelect,
  } = useStyles();

  useEffect(() => {
    // dispatch(resetRegisterResponse());
    dispatch(resetLoaders());
    getGeoInfo();

    if (token) {
      navigate(`${GLOBAL_PREFIX}/dashboard`);
    }

    // if (registerResponse) {
    //     navigate(`/email/${registerResponse?.userId}`, {state: registerResponse?.userId});
    //     toast({
    //         position: "top",
    //         title: "Account created.",
    //         description: "Please verify your account.",
    //         status: "success",
    //         duration: 9000,
    //         isClosable: true,
    //     });
    //     dispatch(resetErrorMsg());
    // }
  }, []);

  const [country, setCountry] = useState<string>("United States of America");

  const getGeoInfo = () => {
    axios
      .get("https://ipapi.co/json/")
      .then((response) => {
        let data = response.data;
        setCountry(data.country_name);
        // alert(JSON.stringify(data.country_name));
      })
      .catch((error: any | unknown) => {
        console.log(error);
      });
  };

  const onSubmit = async ({ email, password, firstname, lastname }: FormikValues) => {
    if (!online) return;
    const { payload, error }: any = await dispatch(
      registerFn({ firstname, lastname, email, password })
    );
    // console.log(payload, "YYYYYY")
    if (error) {
      console.log(error);

      return setErrorMsg(error?.message);
    }
    if (payload) {
      setCodeJwt(payload);
      onOpen();
    }
  };

  async function SignUpWithGoogle() {
    try {
      setGoogleLoading(true);
      // const { data }: any = await axios(config);
      // console.log(data,"hhhhhhhh")
      // if (data) {
      //   window.location.replace(data.url);
      window.location.replace(`${BASE_URL}/user/google`);
      // }
    } catch (e: any | unknown) {
      console.log(e, "err google");
    } finally {
      setGoogleLoading(false);
    }
  }

  async function SignUpWithTwitter() {
    try {
      // setGoogleLoading(true);
      window.location.replace(`${BASE_URL}/users/twitter`);

      // const { data }: any = await axios(config);
      // //   console.log(data, "hhhhhhhh");
      // if (data) {
      //   window.location.replace(data.url);
      // }
    } catch (e: any | unknown) {
      console.log(e, "err twitter");
    } finally {
      setGoogleLoading(false);
    }
  }

  async function SignUpWithFacebook() {
    try {
      // setGoogleLoading(true);
      window.location.replace(`${BASE_URL}/users/facebook`);

      // const { data }: any = await axios(config);
      // //   console.log(data, "hhhhhhhh");
      // if (data) {
      //   window.location.replace(data.url);
      // }
    } catch (e: any | unknown) {
      console.log(e, "err twitter");
    } finally {
      setGoogleLoading(false);
    }
  }
  return (
    <>
      <Suspense fallback={<Spinner size="sm" />}>
        <Flex pos={"absolute"}>
          <TopIconBar pos={"fixed"} />
        </Flex>
      </Suspense>
      <Flex {...root}>
        <Flex {...boxLeft}>
          <Image src={Images_Icons.AuthImage} alt="Me Protocol" {...leftImg} />
        </Flex>

        <Flex {...boxRegister} overflowX="hidden">
          <Formik
            initialValues={{
              email: "",
              password: "",
              confirm: "",
              firstname: "",
              lastname: "",
            }}
            validationSchema={RegisterValidationSchema}
            onSubmit={onSubmit}
          >
            {(formik) => (
              <VStack
                as={"form"}
                {...registerColumn}
                onChange={() => dispatch(resetErrorMsg())}
                //@ts-ignore
                onSubmit={formik.handleSubmit}
              >
                <Text {...head}>Let’s get started.</Text>
                <Flex w={"100%"} justify={"space-between"}>
                  <Suspense fallback={<Spinner size="sm" />}>
                    <Link href={`${BASE_URL}/user/google`} w="full">
                      <MeProtocolButton
                        isLoading={googleLoading}
                        name={"Sign up with Google"}
                        // onClick={SignUpWithGoogle}
                        rest={{ w: "100%" }}
                      />
                    </Link>
                  </Suspense>
                  {/* <Flex w={"28%"} justify={"space-between"}>
                    <Center
                      as={Button}
                      bg={"#F6F6F6"}
                      h={"45px"}
                      w={"48%"}
                      borderRadius={"25px"}
                      onClick={SignUpWithFacebook}
                      _hover={{ bg: "black", color: "light" }}
                      role="group"
                    >
                      <Icon
                        color={"#999999"}
                        w={"20px"}
                        h={"20px"}
                        as={FaFacebookSquare}
                        _groupHover={{ color: "light" }}
                      />
                    </Center>

                    <Center
                      as={Button}
                      bg={"#F6F6F6"}
                      h={"45px"}
                      w={"48%"}
                      borderRadius={"25px"}
                      onClick={SignUpWithTwitter}
                      _hover={{ bg: "black", color: "light" }}
                      role="group"
                    >
                      <Icon
                        color={"#999999"}
                        w={"20px"}
                        h={"20px"}
                        as={FaTwitter}
                        _groupHover={{ color: "light" }}
                      />
                    </Center>
                  </Flex> */}
                </Flex>
                <Flex align={"center"} w={"100%"} justify={"space-between"}>
                  <Divider w={"45%"} color={"#ECECEC"} />
                  <Text color={"#999999"} fontSize={{ base: "12px", md: "20px" }}>
                    or
                  </Text>
                  <Divider w={"45%"} color={"#ECECEC"} />
                </Flex>
                <Text {...desc}>Enter a few details to join the ME community</Text>
                <Flex
                  flexDir={{ base: "column", md: "row" }}
                  justifyContent={"space-between"}
                  w={"100%"}
                  // align={"center"}
                >
                  <Flex w={{ base: "100%", md: "49%" }}>
                    <Suspense fallback={<Spinner size="sm" />}>
                      <MeProtocolInput name={"firstname"} placeholder={"First Name"} />
                    </Suspense>
                  </Flex>
                  <Flex w={{ base: "100%", md: "49%" }}>
                    <Suspense fallback={<Spinner size="sm" />}>
                      <MeProtocolInput name={"lastname"} placeholder={"Last Name"} />
                    </Suspense>
                  </Flex>
                </Flex>

                <Suspense fallback={<Spinner size="sm" />}>
                  <MeProtocolInput name={"email"} type={"email"} placeholder={"your@email.com"} />
                  {errorMsg && (
                    <Text fontSize={"12px"} color={"red"}>
                      {errorMsg}
                    </Text>
                  )}
                </Suspense>

                <Heading {...basedText}>Get yourself a password.</Heading>
                <Text {...desc}>
                  Passwords must include a symbol or number and have at least 8 characters.
                </Text>

                <Suspense fallback={<Spinner size="sm" />}>
                  <MeProtocolInput
                    name={"password"}
                    type={show ? "text" : "password"}
                    placeholder={"Password"}
                  />
                  <MeProtocolInput
                    name={"confirm"}
                    type={show ? "text" : "password"}
                    placeholder={"Confirm Password"}
                  />
                </Suspense>
                <Text
                  as={Button}
                  onClick={handleClick}
                  alignSelf={"flex-end"}
                  {...basedText}
                  pt={"0px"}
                  bg={"transparent"}
                  _hover={{ bg: "transparent" }}
                >
                  {!show ? "Show" : "Hide"}
                </Text>

                <Text {...basedText}>Where are you based?</Text>
                <Suspense fallback={<Spinner size="sm" />}>
                  <CountrySelector
                    value={country}
                    onChange={(e: any) => {
                      setCountry(e.target.value);
                    }}
                    {...countrySelect}
                  />
                </Suspense>
                <Flex {...checkBoxRoot}>
                  <Checkbox w={"100%"} borderColor={"black"} spacing={"1rem"}>
                    Get exclusive offers, trends and deals
                  </Checkbox>
                </Flex>

                <Suspense fallback={<Spinner size="sm" />}>
                  <MeProtocolFormikButton isLoading={registerLoading} name={"Create Account"} />
                </Suspense>
                <Text {...desc} pt={"10px"}>
                  By signing up you accept to our{" "}
                  <Link href="https://google.com" textDecoration={"underline"}>
                    {" "}
                    Terms and Services.
                  </Link>
                </Text>

                <Text {...desc} pt={"10px"}>
                  Already have an account?
                  <Link
                    onClick={() => {
                      navigate(`${GLOBAL_PREFIX}/login`, { replace: true });
                    }}
                    textDecoration={"underline"}
                  >
                    {" "}
                    Login here.
                  </Link>
                </Text>

                <Box bg="transparent" pt="40px" w="100%" />
              </VStack>
            )}
          </Formik>
        </Flex>
      </Flex>
      <OTPModal isOpen={isOpen} onClose={onClose} token={codeJwt} />
    </>
  );
};

export default Register;
