import {
  Box,
  Flex,
  PinInput,
  Text,
  HStack,
  PinInputField,
  useToast,
  Spinner,
} from "@chakra-ui/react";
import { nanoid } from "@reduxjs/toolkit";
import { IoCloseCircle } from "react-icons/io5";
import { useState } from "react";
// import { RESEND_CODE_FN, VERIFY_EMAIL_FN } from "@/store/AuthSlice";
// import { useRouter } from "next/navigation";
import NewModalComponent from "../../../../components/NewModalComponent";
import MeProtocolToast from "../../../../hooks/MeProtocolToast";
import { useDispatch } from "react-redux";
import NewCountdownTimer from "../../../../components/NewCountdownTimer";
import { resendCodeFn, verifyEmailFn } from "../../../../redux/AuthSlice";
import { useNavigate } from "react-router-dom";
import { GLOBAL_PREFIX } from "../../../../constants";

interface OTPModalProps {
  isOpen: boolean;
  onClose(): void;
  token: string;
}

const OTPModal = ({ isOpen, onClose, token }: OTPModalProps) => {
  const [resendActive, setResendActive] = useState(false);
  const [loading, setLoading] = useState(false);
  const [countFrom, setCountFrom] = useState(60);
  const [errorMsg, setErrorMsg] = useState("");

  let navigate = useNavigate();

  let toast = useToast();

  const dispatch = useDispatch();
  // const router = useRouter();

  async function VERIFY_CODE(code: string) {
    try {
      setLoading(true);

      const { payload, error }: any = await dispatch(
        verifyEmailFn({ code, token })
      );
      if (error) {
        return setErrorMsg(error.message);
      }

      if (payload) {
        onClose();
        // MeProtocolToast(toast, "Sign up successful", 9000, "bottom-left");
        const id = "error-toast";
        if (!toast.isActive(id)) {
          toast({
            id,
            position: "top",
            title: `Account Created Successfully`,
            description:
              "Your account was created using email and password, please enter your email and password to continue",
            status: "success",
            duration: 9000,
            isClosable: false,
          });
        }
        navigate(`${GLOBAL_PREFIX}/login`);
      }
    } catch (error) {
      // console.log(error, "VERIFY_CODEERR");
    } finally {
      setLoading(false);
    }
  }

  async function RESEND_CODE() {
    try {
      setLoading(true);

      const { payload, error }: any = await dispatch(resendCodeFn({ token }));
      if (error) {
        return setErrorMsg(error.message);
      }

      if (payload) {
        MeProtocolToast(toast, "Code resent successfully", 9000, "bottom-left");
      }
    } catch (error) {
      // console.log(error, "VERIFY_CODEERR");
    } finally {
      setLoading(false);
    }
  }

  return (
    <NewModalComponent
      isOpen={isOpen}
      onClose={onClose}
      closeOnOverlayClick={false}
      modalContentStyle={{
        borderRadius: "10px",
      }}
    >
      <Flex flexDir={"column"} py="5px">
        <Flex alignSelf={"flex-end"} as="button" onClick={onClose} mb="20px">
          <IoCloseCircle size={20} />
        </Flex>
        <Text fontSize={"14px"} mb="10px">
          Please provide the OTP sent to your Email
        </Text>

        <HStack w={"100%"} h={"80px"}>
          <PinInput
            onComplete={(e) => {
              setErrorMsg("");
              VERIFY_CODE(e);
            }}
            otp
            placeholder={"*"}
          >
            {[1, 2, 3, 4]?.map(() => (
              <PinInputField
                key={nanoid()}
                bg={"grey"}
                borderRadius={10}
                borderWidth={0}
                w={"30%"}
                h={"100%"}
              />
            ))}
          </PinInput>
        </HStack>
        {errorMsg && <Text color={"red"}>{errorMsg}</Text>}
        {loading && <Spinner size={"sm"} alignSelf={"flex-end"} mt="10px" />}
        <HStack fontSize={"14px"} mt="25px">
          <Text>Did not receive OTP?</Text>

          <Box
            opacity={resendActive ? 1 : 0.5}
            // as="button"
            onClick={() => {
              if (!resendActive) return;
              RESEND_CODE();
              setCountFrom(countFrom + 1);
              setResendActive(false);
            }}
          >
            <NewCountdownTimer
              countFrom={countFrom}
              onCountdownComplete={() => {
                setResendActive(true);
              }}
            />
          </Box>
        </HStack>
      </Flex>
    </NewModalComponent>
  );
};

export default OTPModal;
