import { Modal, ModalBody, ModalContent, ModalOverlay } from "@chakra-ui/react";
import { FC, ReactNode } from "react";

interface ModalComponentProps {
  children: ReactNode;
  isOpen: boolean;
  onClose(): void;
  modalContentStyle?: any;
  rest: any;
}

const NewModalComponent: FC<any> = ({
  children,
  isOpen,
  onClose,
  modalContentStyle,
  modalBodyStyle,
  ...rest
}): JSX.Element => {
  return (
    <Modal
      isCentered
      blockScrollOnMount={true}
      scrollBehavior={"inside"}
      isOpen={isOpen}
      size={"lg"}
      onClose={onClose}
      motionPreset="slideInBottom"
      // useInert="false"
      {...rest}
    >
      <ModalOverlay bg="#00000020" backdropFilter="auto" backdropBlur="2px" />
      <ModalContent
        w={{ base: "90vw", md: "60vw" }}
        borderRadius={0}
        py="1%"
        {...modalContentStyle}
      >
        <ModalBody {...modalBodyStyle}>{children}</ModalBody>
      </ModalContent>
    </Modal>
  );
};

export default NewModalComponent;
