import { Text, Button, HStack } from "@chakra-ui/react";
import React, { useState, useEffect, FC } from "react";

interface CountdownProps {
  countFrom: number;
  onCountdownComplete: () => void;
}

const NewCountdownTimer: FC<CountdownProps> = ({
  countFrom,
  onCountdownComplete,
}) => {
  const [countdown, setCountdown] = useState(countFrom);
  const [isActive, setIsActive] = useState(true); // New state for countdown activity

  useEffect(() => {
    if (isActive && countdown > 0) {
      const interval = setInterval(() => {
        setCountdown(countdown - 1);
      }, 1000);
      return () => clearInterval(interval);
    } else if (countdown === 0 && isActive) {
      setIsActive(false);
      onCountdownComplete();
    }
  }, [countdown, isActive, onCountdownComplete]);

  const formatTime = (time: number) => {
    const minutes = Math.floor(time / 60);
    const seconds = time % 60;
    return `${minutes.toString().padStart(2, "0")}:${seconds
      .toString()
      .padStart(2, "0")}`;
  };

  const handleResetClick = () => {
    if (countFrom > 0) return;
    setCountdown(countFrom);
    setIsActive(true);
  };

  return (
    <HStack as={"button"} onClick={handleResetClick}>
      <Text>Resend</Text>
      <Text opacity={countdown > 0 ? 1 : 0}>({formatTime(countdown)})</Text>
    </HStack>
  );
};

export default NewCountdownTimer;
